.portfolio {
    &__gnb {
        display: flex;
        // display: grid;
        // grid: 48px / 48px auto 48px;
        margin-left: auto;
        margin-right: auto;
        padding-left: 8px;
        padding-right: 8px;
        border-top: 10px solid #fff;
        border-bottom: 10px solid #fff;
    }
    &__home {
        // height: 100%;
        padding-left: 8px;
        padding-right: 8px;
        .content {
            &__title {
                text-align: right;
                h2 {
                    margin: 0;
                    font-family: "Black Han Sans", sans-serif;
                    font-size: 30px;
                    color: #036635;
                }
                p {
                    font-size: 16px;
                    color: #000;
                }
            }
            &__slide {
                // height: calc(100vh - 182px);
                padding-top: 15px;
                padding-bottom: 15px;
                .swiper-container {
                    overflow: hidden;
                    width: 100%;
                    height: 100%;
                    .swiper-slide {
                        overflow: hidden;
                        padding: 0 20px;
                        border-radius: 15px;
                        background: #11998e;
                        background: linear-gradient(to bottom, #38ef7d, #11998e);
                    }
                    .innerSlide {
                        overflow: hidden;
                        position: relative;
                        width: 100%;
                        padding-top: 20px;
                        border-radius: 12px;
                        .thumb {
                            img {
                                width: 100%;
                                height: 100%;
                                vertical-align: top;
                            }
                        }
                        .swiper-slide {
                            padding: 0;
                        }
                        .swiper-button-next,
                        .swiper-button-prev {
                            color: #fff;
                            // Navigation Bug fix.
                            backface-visibility: hidden; 
                            transform: translateZ(0); 
                            transform: translate3d(0, 0, 0);
                        }
                        &--box {
                            padding: 20px 0;
                            a {
                                text-decoration: underline;
                            }
                        }
                        &--content {
                            h3 {
                                margin: 0 0 10px 0;
                                color: #fff;
                                font-size: 16px;
                            }
                            dl {
                                margin: 0;
                                dt, dd {
                                    color: #fff;
                                }
                                dt {
                                    min-width: 70px;
                                    float: left;
                                }
                                dd {
                                    overflow: hidden;
                                    white-space: nowrap;
                                    text-align: right;
                                    text-overflow: ellipsis;
                                }
                            }
                        }
                        &--button {
                            // position: absolute;
                            // bottom: 20px;
                            // left: 20px;
                            // right: 20px;
                            padding-top: 10px;
                            text-align: center;
                            .more__button {
                                display: block;
                                padding: 10px;
                                background: #fff;
                                border: 0;
                                border-radius: 12px;
                                appearance: none;
                                text-decoration: none;
                            }
                        }
                    }
                }
            }
        }
    }
}

.gnb {
    margin-left: 20px;
    &__logo {
        overflow: hidden;
        width: 48px;
        height: 48px;
        border-radius: 100%;
        background-color: yellow;
        img {
            width: 100%;
            height: 100%;
        }
    }
    &__list {
        display: flex;
    }
    &__item {
        margin-right: 8px;
    }
    &__link {
        display: inline-block;
        position: relative;
        padding: 0 8px;
        font-size: 14px;
        font-weight: 500;
        color: #000;
        line-height: 48px;
        text-align: center;
    }
}

.drawer {
    margin-left: auto;
    .nav__button {
        overflow: hidden;
        position: relative;
        width: 48px;
        height: 48px;
        padding: 0 16px;
        border: 0;
        background: transparent;
        color: transparent;
        white-space: nowrap;
        text-align: left;
        line-height: 48px;
        -webkit-tap-highlight-color: initial;
        &::after {
            content: '';
            position: absolute;
            height: 2px;
            top: 18px;
            left: 16px;
            right: 16px;
            vertical-align: top;
            background: #000;
            box-shadow: 0 5px #000, 0 10px #000;
        }
    }
}